  },
  methods:{
	  add(){
		  this.show();
	  },
	  addmess(){
		  var err = {
		      msg: '',
		      ok: true
		  }
		  if(this.content.length<1){
		  	err.ok = false;
		  	err.msg = "内容不能为空"
		  }
		  if (!err.ok) {
		      uni.showModal({
		          title: '提示',
		          content: err.msg
		      })
		      return;
		  }

		  var value = uni.getStorageSync('token')
		  uni.request({
		      url: config.httpUrl +'/api/v1/commodity/transportlog',
		  	method:"POST",
		      header: {
		  	   'Authorization' : value,
		      },
		  	data:{
		  		'oid':this.oid, //0代表所有id
		  		'content':this.content,
				'phone':this.phone
		  	},
		      success: (res) => {
		  		var jsondata=res.data
		  		if(jsondata.code=='0'){
		  			//console.log('更新物流信息成功')
					this.getall()  	
					this.close()
		  		}else{
		  			 config.txtshow(jsondata.msg)				  				
		  		}														
		      },
		  	fail: () =>{  
		  		 config.txtshow('网络异常')
		       }, 
		  });
	  },
	  add0(m){
		  return m<10?'0'+m:m 
		  },
	  format(shijianchuo){
	  //shijianchuo是整数，否则要parseInt转换
	  var time = new Date(shijianchuo);
	  var y = time.getFullYear();
	  var m = time.getMonth()+1;
	  var d = time.getDate();
	  var h = time.getHours();
	  var mm = time.getMinutes();
	  var s = time.getSeconds();
	  return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
	  },
	  getall(){
		  var value = uni.getStorageSync('token')
		  uni.request({
		      url: config.httpUrl +'/api/v1/commodity/transportloglist',
		  	method:"GET",
		      header: {
		  	   'Authorization' : value,
		      },
		  	data:{
		  		'oid':this.oid, //0代表所有id
		  	},
		      success: (res) => {
		  		var jsondata=res.data
		  		if(jsondata.code=='0'){
					//console.log(jsondata.data)
					var curPageData=[]
					for (var i = 0;i<jsondata.data.length; i++) {						
						curPageData.push({
							oid:jsondata.data[i]["oid"],
							acceptStation:jsondata.data[i]["content"],
							phone:jsondata.data[i]["phone"],
							status:'DELIVERING',
							statusName:'运输中',
							isMainNode:false,
							createTime:this.format(jsondata.data[i]["time"]*1000),

						})
					}
					this.tracesData=curPageData
			
		  		}else{
		  			 config.txtshow(jsondata.msg)				  				
		  		}	
		  									
		      },
		  	fail: () =>{  
		  		 config.txtshow('网络异常')
		       }, 
		  });
	  },
	  
	  show: function() {
	              this.$refs.luPopupWrapper.show();
	          },
	   close: function() {
	              this.$refs.luPopupWrapper.close();
	          },
	   closeCallback:function() {
	              // console.log("关闭后回调");
	          }
	  
